import { bindActionCreators, compose } from 'redux';
import { connect } from 'react-redux';

import injectReducer from '../../utils/injectReducer';
import reducer, { setAnswer, startViewQuestion } from './reducer';

const mapStateToProps = (state) => {
  const answers = state.getIn(['questions', 'answers']);
  const startTimes = state.getIn(['questions', 'startTimes']);
  return {
    answers: answers ? answers.toJS() : [],
    score: answers ? answers.filter((a) => a && a[1]).size : 0,
    startTimes,
  };
};

const mapDispatchToProps = (dispatch) => bindActionCreators({
  setAnswer,
  startViewQuestion,
}, dispatch);

export const withReducer = injectReducer({ key: 'questions', reducer });

export const withConnect = connect(mapStateToProps, mapDispatchToProps);

export default compose(withReducer, withConnect);
