import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';

import withReducer from './hoc';
import questions from './questions.json';

import Layout from '../Layout';

import Flex from '../../components/Flex';
import Box from '../../components/Box';
// import Text from '../../components/Text';
import Container from '../../components/Container';
import VerticalCenter from '../../components/VerticalCenter';
import Button from '../../components/Button.js';
import BackgroundImage from '../../components/BackgroundImage.js';
import OverlayWithScroll from '../../components/OverlayWithScroll.js';
import Circle from '../../components/Circle.js';
import Link from '../../components/Link.js';
import { mobileOrDesktop } from '../../components/ThemeProvider/theme.js';

class QuestionPage extends PureComponent {
  static getDerivedStateFromProps(nextProps) {
    const question = nextProps.pageContext;
    const pageId = question.id;
    const qId = question.id - 1;

    return {
      question,
      pageId,
      qId,
    }
  }

  state = {
  }

  componentDidMount() {
    this.props.startViewQuestion(this.state.qId);
  }

  handleClose = () => {
    this.setState({ showAnswer : false })
  }

  handleChoose = (value) => {
    const { id, correct } = this.state.question
    if (window.ga) {
      window.ga('send', 'event', '回答問題', `第${id}題`, value);
    }
    this.setState({ showAnswer: true, select: value, chooseAnswer: true })
    this.props.setAnswer(id - 1, value, value === correct)
  }

  handleLearnMore = () => {
    this.setState(({ showAnswer }) => ({ showAnswer: !showAnswer }))
  }

  render() {
    const {
      showAnswer,
      select,
      question,
      chooseAnswer,
      qId,
      pageId,
    } = this.state;

    return (
      <Layout>
        <Container textAlign="center" height="100vh">
          <VerticalCenter>
            <Flex justifyContent="center" my="1em">
              {questions.map((q, index) => (
                <Flex alignItems="center">
                  <Circle width="1em" bg={index === qId ? 'black' : 'gray'} />
                  {questions.length - 1 !== index && <Box bg="gray" height="2px" px="2em" />}
                </Flex>
              ))}
            </Flex>
            <Flex
              mb={mobileOrDesktop('2rem', '2em')}
              justifyContent="center"
              alignItems="center"
              fontSize={mobileOrDesktop('1.5em', '2em')}
              transform="translateX(-0.5em)"
            >
              <Circle width="2.15em" bg="gray" transform="translateX(1em)">Q{pageId}</Circle>
              <Box bg="gray" px="2em" py="0.5em">{questions[qId].title}</Box>
            </Flex>
            <Flex flexDirection={mobileOrDesktop('column', 'row')} justifyContent="center">
              <Box
                width={mobileOrDesktop(1, 3 / 5)}
                mx={mobileOrDesktop('0', '0.5em')}
                mb={mobileOrDesktop('2em', '0')}
                position="relative"
              >
                <BackgroundImage src="https://loremflickr.com/1920/1080" ratio={9 / 16} borderRadius="2em">
                  {showAnswer && (
                    <OverlayWithScroll onRequestClose={this.handleClose}>
                      解傳吃傳來不二：一禮體多理？院也一界起司眼當，由十白局、日前法服！我完分性質夫住性門客，師家特化。類面本少有，這速世關之河畫你提情身。不母程到，邊大讓其；西子中美會不來息，臺到的行令。道有種助中打電。成文除英。熱多獨今文。都麗可畫了事由。個張其系者成阿阿阿！只口生風英預異到手程市特別說題麼兒有相列！月買人生人：整外城有的縣一費合流歡聲，入教文城長……為老都想變山。連願界成否出風師麼草重還；好情小藥是我是的家山重關登後無失到向著……共施也。高舉教始年頭動們之推麼形獨育了。廠倒急突從財是結、上商校展流來接受起愛望必驗？海了的各拉黨多歡創語業值了已進，家自光臺際種竟行生上歡角流，回師集的影地生趣。下了用。
                    </OverlayWithScroll>
                  )}
                </BackgroundImage>
              </Box>
              <Box m="0.5em">
                {questions[qId].options.map((q, index) => (
                  <Box mb="0.75em" key={index}>
                    <Button
                      onClick={() => this.handleChoose(index)}
                      disabled={chooseAnswer}
                      selected={select === index}
                      correct={questions[qId].correct === index}
                      width={mobileOrDesktop('14.5em', '16.5em')}
                    >
                      {q}
                    </Button>
                  </Box>
                ))}
                <Flex mt={mobileOrDesktop('1em', '2em')} justifyContent="center">
                  <Button
                    mx="0.25em"
                    width={mobileOrDesktop('7em', '8em')}
                    onClick={this.handleLearnMore}
                  >
                    深入了解
                  </Button>
                  <Button
                    mx="0.25em"
                    width={mobileOrDesktop('7em', '8em')}
                    is={Link}
                    to={question.isLast ? '/destination' : `question/${pageId + 1}`}
                  >
                    {question.isLast ? '看結果' : '下一題'}
                  </Button>
                </Flex>
              </Box>
            </Flex>
          </VerticalCenter>
        </Container>
      </Layout>
    );
  };
}

QuestionPage.propTypes = {
  pageContext: PropTypes.object,
  answers: PropTypes.array,
  setAnswer: PropTypes.func,
};

export default withReducer(QuestionPage);
