import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import { IoIosClose } from 'react-icons/io';
import { Scrollbars } from 'react-custom-scrollbars';
import { withContentRect } from 'react-measure';

import Box from './Box';
import Button from './Button';
import Text from './Text';

class OverlayWithScroll extends PureComponent {
  componentDidMount() {
    this.props.measure();
  }

  render() {
    const {
      measureRef,
      contentRect: { bounds: { height } },
      onRequestClose,
      children,
    } = this.props;
    console.log(height)
    return (
      <div>
        <Box
          position="absolute"
          top="0"
          bottom="0"
          left="0"
          right="0"
          bg="rgba(220,220,220,0.3)"
          py="3em"
          px="2em"
          borderRadius="2em"
        >
          <Box height="100%" ref={measureRef}>
            <Scrollbars style={{ height }} autoHide>
              <Text color="white" textAlign="left">
                {children}
              </Text>
            </Scrollbars>
          </Box>
        </Box>
        <Box position="absolute" top="-0.25em" right="0.25em">
          <Button.transparent
            border="transparent"
            hoverBg="transparent"
            color="white"
            onClick={onRequestClose}
          >
            <IoIosClose size="2em" />
          </Button.transparent>
        </Box>
      </div>
    );
  }
}

OverlayWithScroll.propTypes = {

};

export default withContentRect('bounds')(OverlayWithScroll);
